button {
  box-sizing: border-box;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  color: #e74c3c;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #e74c3c;
  border-radius: .6em;
  align-self: center;
  margin: 20px;
  padding: 1.2em 2.8em;
  font-family: Montserrat, sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  text-decoration: none;
  display: flex;
}

button:hover, button:focus {
  color: #000;
  outline: 0;
}

.start {
  color: #f1c40f;
  background-image: linear-gradient(45deg, #3498db 50%, rgba(0, 0, 0, 0) 50%);
  background-position: 100%;
  background-size: 400%;
  border-color: #f1c40f;
  transition: background .3s ease-in-out;
}

.start:hover {
  background-position: 0;
}

.stop {
  color: #3498db;
  background-image: linear-gradient(45deg, #f1c40f 50%, rgba(0, 0, 0, 0) 50%);
  background-position: 100%;
  background-size: 400%;
  border-color: #3498db;
  transition: background .3s ease-in-out;
}

.stop:hover {
  background-position: 0;
}

.disabled {
  opacity: .2;
}

